
import { defineComponent, ref } from 'vue'

export default defineComponent({
  name: 'CustomDropdown',
  props: {
    openOnHover: {
      type: Boolean,
      default: false,
    },
  },
  setup (_) {
    const opened = ref(false)
    const setOpened = (val: boolean) => opened.value = val
    const onHover = () => {
      if (_.openOnHover)
        setOpened(true)
    }

    return {
      opened,
      setOpened,
      onHover,
    }
  },
})
